import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { GeneralService } from '../services/general.service';
import { MainService } from '../services/main.service';

@Component({
  selector: 'ngx-consumer-detail',
  templateUrl: './consumer-detail.component.html',
  styleUrls: ['./consumer-detail.component.scss'],
})
export class ConsumerDetailComponent implements OnInit {

  /** API URLS */
  readonly API_USUARIO = 'api/user';
  readonly API_RECOMPENSA_BY_USER = 'api/recompensa/user';

  /** Loading handler */
  loading = false;

  /** consumer object */
  consumer: any = {};

  /** array of interests */
  interests = [];

  /** Table source */
  source = [];

  /** table settings */
  settings = {
    actions: {
      columnTitle: 'Acciones',
      filter: true,
      add: false,
      edit: false,
      delete: false,
      custom: [
        {
          name: 'ver',
          title: '<i class="fa fa-search" title="Ver publicación"></i>',
        },
      ],
    },
    columns: {
      sector: {
        title: 'Intereses',
        valuePrepareFunction: (value) => {
          return value.nombre;
        },
      },
      ranking: { title: 'Ranking'},
      creditos: { title: 'Créditos',
      valuePrepareFunction: (value) => {
        return value.toLocaleString('en-US', {
          style: 'currency',
          currency: 'USD',
        });
      }},
    },
    mode: 'external',
  };

  constructor(private mainService: MainService, private activatedRoute: ActivatedRoute, private generalService: GeneralService) {}

  async ngOnInit() {
    this.loading = true;
    const userid = this.activatedRoute.snapshot.paramMap.get('user');
    await this.fetchUser({uid: userid});
    await this.fetchInterests({uid: userid});
    this.loading = false;
  }

  /**
   * Fetchs user from backend
   * @param uid user id
   */
  fetchUser({uid}): void {
    // this.loading = true;
    this.mainService.get({api: `${this.API_USUARIO}/${uid}`}).then((value) => {
      this.consumer = value;
    }).finally(() => {
    });
  }

  /**
   * Fetchs interests of user from backend
   * @param uid user id
   */
  fetchInterests({uid}): void {
    this.mainService.get({api: `${this.API_RECOMPENSA_BY_USER}/${uid}`}).then((value) => {
      this.interests = value;
      this.source = this.interests;
    }).finally(() => {

    });
  }

  /**
   * Navigates back
   */
  goBack() {
    this.generalService.goBack();
  }

  /**
   * Handles click on custom action
   * @param event of the table
   */
  onCustomAction({ event }): void {}
}
