import { Component, OnInit } from "@angular/core";
import { MainService } from "../services/main.service";

@Component({
  selector: "ngx-empresas",
  templateUrl: "./empresas.component.html",
  styleUrls: ["./empresas.component.scss"],
})
export class EmpresasComponent implements OnInit {
  /** API URLS */
  readonly API_EMPRESAS = "api/empresa";

  /** Controller for loading */
  loading = false;

  /** Object containing all related to empresas */
  empresas = {
    approved: {
      settings: {
        actions: {
          columnTitle: "Acciones",
          filter: true,
          add: false,
          edit: false,
          delete: false,
          custom: [
            {
              name: "publicaciones",
              title:
                '<i class="fa fa-book-open" title="Ver publicaciones"></i>',
            },
            {
              name: "ver",
              title: '<i class="fa fa-search" title="Ver empresa"></i>',
            },
            {
              name: "rechazar",
              title: '<i class="fa fa-trash" title="Borrar"></i>',
            },
          ],
        },
        columns: {
          nombre: { title: "Nombre", width: "80%" },
        },
        mode: "external",
      },
      source: [],
    },
    pending: {
      settings: {
        actions: {
          columnTitle: "Acciones",
          filter: true,
          add: false,
          edit: false,
          delete: false,
          custom: [
            {
              name: "ver",
              title: '<i class="fa fa-search" title="Ver empresa"></i>',
            },
            {
              name: "rechazar",
              title: '<i class="fa fa-times" title="Eliminar"></i>',
            },
            {
              name: "aprobar",
              title: '<i class="fa fa-check" title="Aprobar"></i>',
            },
          ],
        },
        columns: {
          nombre: { title: "Nombre", width: "80%" },
        },
        mode: "external",
      },
      source: [],
    },
  };

  constructor(private mainService: MainService) {}

  ngOnInit(): void {
    this.fetchEmpresas();
  }

  /**
   * Fetchs empresas from backend
   */
  fetchEmpresas() {
    this.loading = true;
    this.mainService
      .get({ api: this.API_EMPRESAS, hide404: true })
      .then((value) => {
        this.setEmpresasTable({ data: value });
      })
      .finally(() => {
        this.loading = false;
      });
  }

  /**
   * Sets and organizes approved and pending empresas data in tables
   * @param raw data from backend
   */
  setEmpresasTable({ data }) {
    this.empresas.approved.source = data.filter((e) => {
      if (e.estado === "aprobado") {
        return true;
      } else {
        return false;
      }
    });
    this.empresas.pending.source = data.filter((e) => {
      if (e.estado === "pendiente") {
        return true;
      } else {
        return false;
      }
    });
  }
}
