import { Component, OnInit } from "@angular/core";
import { LocalDataSource } from "ng2-smart-table";
import { GeneralService } from "../../../services/general.service";
import { MainService } from "../../../services/main.service";

@Component({
  selector: "ngx-secciones",
  templateUrl: "./secciones.component.html",
  styleUrls: ["./secciones.component.scss"],
})
export class SeccionesComponent implements OnInit {
  /** Arrya with secciones */
  public secciones: any = [];

  /** Source for table */
  public source: LocalDataSource;

  /** Settings configuration for table */
  public settings: any = {};

  constructor(
    private mainService: MainService,
    private generalService: GeneralService
  ) {}

  ngOnInit(): void {
    this.getSecciones();
    this.setSettings();
  }

  /**
   * Gets all publicaciones
   */
  public getSecciones() {
    this.mainService.get({ api: "api/seccion" }).then((res) => {
      console.log(res);
      this.secciones = res;
      this.setAttributes();
      this.source = new LocalDataSource(this.secciones);
    });
  }

  /**
   * Table custom action handler
   * @param event of the table
   */
  onCustomAction({ event }): void {
    if (event.action === "ver") {
      this.generalService.routerNavigateTo({
        path: `index/mvp/secciones/${event.data._id}`,
      });
    } else if (event.action === "rechazar") {
      console.log(event);
      this.mainService
        .delete({ api: `api/seccion/${event.data._id}` })
        .then((response) => {
          if (response && response._id) {
            this.generalService.showToastr({
              title: "Sección eliminada",
              status: "success",
            });
            this.getSecciones();
          }
        });
      // this.highlightPost({id: event.data._id, destacar })
    }
  }

  /** Navigates to creation sreen */
  public onRegister() {
    this.generalService.routerNavigateTo({
      path: `index/mvp/destacados/seccion/crear`,
    });
  }

  /**
   * Attributes in publicaciones for table
   */
  public setAttributes() {
    this.secciones.forEach((element) => {
      element["nombreSeccion"] = element.seccion
        ? element.seccion.nombre
        : "N/A";
    });
  }

  /**
   * Settings configuration
   */
  public setSettings() {
    this.settings = {
      actions: {
        columnTitle: "Acciones",
        filter: true,
        add: false,
        edit: false,
        delete: false,
        custom: [
          {
            name: "rechazar",
            title: '<i class="fa fa-trash" title="Borrar"></i>',
          },
        ],
      },
      columns: {
        nombre: { title: "Título" },
        descripcion: { title: "Descripcion" },
        icono: {
          title: "Icono",
          type: "html",
          valuePrepareFunction: (images: string) => {
            return `<img height="50px" class='table-thumbnail-img' src='${images}'/>`;
          },
        },
      },
      mode: "external",
    };
  }
}
