import { Component, OnInit } from "@angular/core";
import { MainService } from "../../services/main.service";
import { GeneralService } from "../../services/general.service";
import { ActivatedRoute } from "@angular/router";
import { AuthService } from "../../services/auth.service";

@Component({
  selector: "ngx-profile",
  templateUrl: "./profile.component.html",
  styleUrls: ["./profile.component.scss"],
})
export class ProfileComponent implements OnInit {
  private readonly API_SECTOR: string = 'api/sector';

  user;

  empresa;

  selectedFile;

  public sectores = [];

  constructor(
    private mainService: MainService,
    private generalService: GeneralService,
    private authService: AuthService,
    private route: ActivatedRoute
  ) {}

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem('_user'));
    this.empresa = this.user.empresa;
    this.getSectores();
  }

  /**
   * Fetchs sectores from backend
   */
   public getSectores() {
    this.mainService.get({ api: this.API_SECTOR }).then((response) => {
      this.sectores = response as [];
    });
  }

    /**
   * Opens logo file selector
   */
     public openFileSelector() {
      document.getElementById('logo').click();
    }

      /**
   * Listens on change of select file
   * @param param0 with event of the input
   */
       public onChangeLogo({ event }) {
        if (event.target.files.length > 0) {
          this.selectedFile =  event.target.files[0];
        }
      }
}
