import { Component, OnInit } from "@angular/core";
import { MainService } from "../services/main.service";

@Component({
  selector: "ngx-usuarios",
  templateUrl: "./usuarios.component.html",
  styleUrls: ["./usuarios.component.scss"],
})
export class UsuariosComponent implements OnInit {
  /** API URLS */
  readonly API_USUARIO = "api/user";

  /** Controller for loading */
  loading = false;

  consumers = {
    settings: {
      actions: {
        columnTitle: "Acciones",
        filter: true,
        add: false,
        edit: false,
        delete: false,
        custom: [
          {
            name: "ver",
            title: '<i class="fa fa-search" title="Ver empresa"></i>',
          },
          {
            name: "borrar",
            title: '<i class="fa fa-trash" title="Borrar"></i>',
          },
        ],
      },
      columns: {
        nombre: { title: "Nombre (s)", width: "30%" },
        apellido: { title: "Apellido", width: "30%" },
        email: { title: "Email", width: "30%" },
      },
      mode: "external",
    },
    source: [],
  };

  empresas = {
    settings: {
      actions: {
        columnTitle: "Acciones",
        filter: true,
        add: false,
        edit: false,
        delete: false,
        custom: [
          {
            name: "ver",
            title: '<i class="fa fa-search" title="Ver empresa"></i>',
          },
          {
            name: "borrar",
            title: '<i class="fa fa-trash" title="Borrar"></i>',
          },
        ],
      },
      columns: {
        nombre: { title: "Nombre (s)", width: "30%" },
        apellido: { title: "Apellido", width: "30%" },
        email: { title: "Email", width: "30%" },
      },
      mode: "external",
    },
    source: [],
  };

  admin = {
    settings: {
      actions: {
        columnTitle: "Acciones",
        filter: true,
        add: false,
        edit: false,
        delete: false,
        custom: [
          {
            name: "ver",
            title: '<i class="fa fa-search" title="Ver empresa"></i>',
          },
          {
            name: "borrar",
            title: '<i class="fa fa-trash" title="Borrar"></i>',
          },
        ],
      },
      columns: {
        nombre: { title: "Nombre (s)", width: "30%" },
        apellido: { title: "Apellido", width: "30%" },
        email: { title: "Email", width: "30%" },
      },
      mode: "external",
    },
    source: [],
  };

  constructor(private mainService: MainService) {}

  ngOnInit(): void {
    this.fetchUsers();
  }

  /**
   * Fetchs users from backend
   */
  fetchUsers() {
    this.loading = true;
    this.mainService
      .get({ api: this.API_USUARIO, hide404: true })
      .then((value) => {
        let consumers = value.filter((user) => {
          return user.tipo === "consumidor";
        });
        let empresas = value.filter((user) => {
          return user.tipo === "administrador";
        });
        let admin = value.filter((user) => {
          return user.tipo === "superadministrador";
        });
        this.setUsersTable({
          consumers: consumers,
          empresas: empresas,
          admin: admin,
        });
      })
      .finally(() => {
        this.loading = false;
      });
  }

  /**
   * Sets and organizes users data in table
   * @param raw data from backend
   */
  setUsersTable({ consumers, empresas, admin }) {
    this.consumers.source = consumers;
    this.empresas.source = empresas;
    this.admin.source = admin;
  }
}
