import { Component, Input, OnInit } from '@angular/core';
import { GeneralService } from '../../services/general.service';

@Component({
  selector: 'ngx-usuarios-table',
  templateUrl: './usuarios-table.component.html',
  styleUrls: ['./usuarios-table.component.scss']
})
export class UsuariosTableComponent implements OnInit {

  /** Table source */
  @Input() source;
  /** Table settings */
  @Input() settings;

  constructor(private generalService: GeneralService) {}

  ngOnInit(): void {}

  /**
   * Detects on change
   * @param newValue injected by component
   */
  ngOnChanges(newValue) {
    this.source = newValue.source.currentValue;
  }

  /**
   * Handles click on any custom action in the table
   * @param event of the table
   */
  onCustomAction({event}): void {
    switch (event.action) {
      case 'ver':
        this.generalService.routerNavigateTo({path: `/index/consumer/${event.data._id}`});
        break;
      default:
        break;
    }
  }
}
