import { Component, OnDestroy, OnInit } from "@angular/core";
import { NbMenuService, NbSidebarService } from "@nebular/theme";
import { LayoutService } from "../../../@core/utils";
import { Subject } from "rxjs";
import { AuthService } from "../../services/auth.service";
import { GeneralService } from "../../services/general.service";

@Component({
  selector: "ngx-header",
  templateUrl: "./header.component.html",
  styleUrls: ["./header.component.scss"],
})
export class HeaderComponent implements OnInit, OnDestroy {
  /** Destroy subject */
  private destroy$: Subject<void> = new Subject<void>();

  /** Acciones usuario */
  public user: any = {};
  public userMenu: { title: string; data: { id: string } }[] = [];

  constructor(
    private sidebarService: NbSidebarService,
    private menuService: NbMenuService,
    private layoutService: LayoutService,
    private authService: AuthService,
    private generalService: GeneralService
  ) {
    this.user = this.authService.getUser();
  }

  ngOnInit() {
    this.onInitMenu();
    this.onSetUser();
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  // =================================
  // Metodos
  // ================================

  /**
   * Inicializa el menú
   */
  private onInitMenu(): void {
    this.userMenu = [
      { title: "Perfil", data: { id: "perfil" } },
      { title: "Cerrar sesión", data: { id: "salir" } },
    ];
    return;
  }

  /**
   * Set usuario en sesión y acciones del menu de usuario
   */
  private onSetUser(): void {
    /** User Profile */
    this.user.picture = this.user.imagen || "";
    if (!this.user.picture) this.user.picture = "assets/images/people.png";

    /** Menu Actions */
    this.menuService.onItemClick().subscribe((menuBag) => {
      if (menuBag && menuBag.item && menuBag.item.data) {
        const action = menuBag.item.data;
        if (`${action.id}`.toLowerCase().includes("perfil")) {
          this.generalService.routerNavQueryTo({
            path: "/index/dashboard/profile/detail",
            query: { _id: this.authService.getUser()._id },
          });
          return;
        } else if (`${action.id}`.toLowerCase().includes("salir")) {
          this.onLogout();
          this.generalService.routerNavigateTo({ path: "/login" });

          return;
        }
      }
    });
  }

  /**
   * Abre o cierra el menú lateral
   */
  public toggleSidebar(): boolean {
    this.sidebarService.toggle(true, "menu-sidebar");
    this.layoutService.changeLayoutSize();
    return false;
  }

  /**
   * Navegación a inicio
   */
  public navigateHome(): boolean {
    this.menuService.navigateHome();
    return false;
  }

  /**
   * Cierra sesión
   */
  public onLogout(): void {
    this.authService.logout();
  }
}
