import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';
import { GeneralService } from '../../services/general.service';
import { MainService } from '../../services/main.service';

@Component({
  selector: 'ngx-empresas-table',
  templateUrl: './empresas-table.component.html',
  styleUrls: ['./empresas-table.component.scss'],
})
export class EmpresasTableComponent implements OnInit, OnChanges {

  /** Triggers update event */
  @Output() update = new EventEmitter<any>();

  /** source of the table */
  @Input() source;
  /** settings of the table */
  @Input() settings;

  ////////////////////// API URLS //
  /** company api url */
  readonly API_EMPRESA = 'api/empresa';

  constructor(
    private generalService: GeneralService,
    private mainService: MainService,
  ) {}

  ngOnInit(): void {}

  /**
   * Detects on change
   * @param newValue injected by component
   */
  ngOnChanges(newValue) {
    this.source = newValue.source.currentValue;
  }

  /**
   * Handles click on any custom action in the table
   * @param event triggered by the table
   */
  onCustomAction({ event }): void {
    switch (event.action) {
      case 'ver':
        this.generalService.routerNavigateTo({
          path: `/index/empresa/${event.data._id}`,
        });
        break;
      case 'aprobar':
        this.approveEmpresa({ id: event.data._id });
        break;
      case 'rechazar':
        this.rejectEmpresa({ id: event.data._id });
        break;
      case 'publicaciones':
        this.generalService.routerNavigateTo({path: `/index/publicaciones/empresa/${event.data._id}`});
        break;
      default:
        break;
    }
  }

  /**
   * Rejects the company
   * @param object with id of the company
   */
  rejectEmpresa({ id }): void {
    const newData = { estado: 'rechazado' };
    this.mainService
      .put({ api: `${this.API_EMPRESA}/${id}`, data: newData })
      .then((value) => {
        if (value) {
          this.generalService.showToastr({
            title: 'Empresa rechazada',
            message: ``,
            status: 'danger',
          });
          this.update.emit();
        }
      });
  }

  /**
   * Approves company
   * @param object with id of the company
   */
  approveEmpresa({ id }): void {
    const newData = { estado: 'aprobado' };
    this.mainService
      .put({ api: `${this.API_EMPRESA}/${id}`, data: newData })
      .then((value) => {
        if (value) {
          this.generalService.showToastr({
            title: 'Empresa aprobada',
            message: ``,
            status: 'success',
          });
          this.update.emit();
        }
      });
  }
}
