import { Component, OnInit } from "@angular/core";
import { NbMenuItem } from "@nebular/theme";
import { AuthService } from "../../services/auth.service";

@Component({
  selector: "ngx-index",
  templateUrl: "./index.component.html",
  styleUrls: ["./index.component.scss"],
})
export class IndexComponent implements OnInit {
  /** Auth */
  private isSuperAdmin: boolean = false;

  /** Menú de la plataforma */
  public menu: NbMenuItem[] = [
    {
      title: "CARGANDO",
      group: true,
    },
  ];

  constructor(private authService: AuthService) {}

  ngOnInit() {
    this.isSuperAdmin =
      JSON.parse(localStorage.getItem("_user")).tipo === "superadministrador";
    this.setUpMenu();
  }

  /**
   * Construye el menu
   */
  private setUpMenu(): void {
    this.menu = [
      {
        title: "Inicio",
        icon: "home-outline",
        link: "/index",
        home: true,
      },
      {
        title: "Usuarios",
        icon: "people-outline",
        link: "usuarios",
        hidden: !this.isSuperAdmin,
      },
      {
        title: "Empresas",
        icon: "briefcase-outline",
        link: "empresas",
        hidden: !this.isSuperAdmin,
      },
      {
        title: "Publicaciones",
        icon: "hash-outline",
        link: "publicaciones",
        hidden: this.isSuperAdmin,
      },
      {
        title: "Sectores",
        icon: "hash-outline",
        link: "publicaciones/sector",
        hidden: !this.isSuperAdmin,
      },
      {
        title: "Notificaciones",
        icon: "bell-outline",
        link: "notificaciones",
        hidden: this.isSuperAdmin,
      },
      {
        title: "MVP",
        children: [
          {
            title: "Publicaciones",
            icon: "image-outline",
            link: "mvp/publicaciones",
            hidden: !this.isSuperAdmin,
          },
          {
            title: "Productos",
            icon: "gift-outline",
            link: "mvp/productos",
            hidden: !this.isSuperAdmin,
          },
          {
            title: "Compras",
            icon: "shopping-cart-outline",
            link: "mvp/compras",
            hidden: !this.isSuperAdmin,
          },
          {
            title: "Destacados",
            icon: "star-outline",
            children: [
              {
                title: "Post",
                link: "mvp/destacados",
                hidden: !this.isSuperAdmin,
              },
              {
                title: "Secciones",
                link: "mvp/destacados/seccion",
                hidden: !this.isSuperAdmin,
              },
            ],
          },
        ],
      },
    ];
  }
}
