import { Component, OnInit } from "@angular/core";
import { LocalDataSource } from "ng2-smart-table";
import { GeneralService } from "../../services/general.service";
import { MainService } from "../../services/main.service";

@Component({
  selector: "ngx-sectores",
  templateUrl: "./sectores.component.html",
  styleUrls: ["./sectores.component.scss"],
})
export class SectoresComponent implements OnInit {
  /** Arrya with sectores */
  public sectores: any = [];

  /** Source for table */
  public source: LocalDataSource;

  /** Settings configuration for table */
  public settings: any = {};

  constructor(
    private mainService: MainService,
    private generalService: GeneralService
  ) {}

  ngOnInit(): void {
    this.getSectores();
    this.setSettings();
  }

  /**
   * Gets all publicaciones
   */
  public getSectores() {
    this.mainService.get({ api: "api/sector" }).then((res) => {
      console.log(res);
      this.sectores = res;
      this.setAttributes();
      this.source = new LocalDataSource(this.sectores);
    });
  }

  /**
   * Table custom action handler
   * @param event of the table
   */
  onCustomAction({ event }): void {
    if (event.action === "ver") {
      this.generalService.routerNavigateTo({
        path: `index/publicaciones/sector/${event.data._id}`,
      });
    } else if (event.action === "rechazar") {
      console.log(event);
      this.mainService
        .delete({ api: `api/sector/${event.data._id}` })
        .then((response) => {
          if (response && response._id) {
            this.generalService.showToastr({
              title: "Sector eliminado",
              status: "success",
            });
            this.getSectores();
          }
        });
    }
  }

  /** Navigates to creation sreen */
  public onRegister() {
    this.generalService.routerNavigateTo({
      path: `index/publicaciones/sector/crear`,
    });
  }

  /**
   * Attributes in publicaciones for table
   */
  public setAttributes() {
    this.sectores.forEach((element) => {
      element["nombreSector"] = element.sector ? element.sector.nombre : "N/A";
    });
  }

  /**
   * Settings configuration
   */
  public setSettings() {
    this.settings = {
      actions: {
        columnTitle: "Acciones",
        filter: true,
        add: false,
        edit: false,
        delete: false,
        custom: [
          {
            name: "rechazar",
            title: '<i class="fa fa-trash" title="Borrar"></i>',
          },
        ],
      },
      columns: {
        nombre: { title: "Nombre" },
        descripcion: {
          title: "Descripcion",
          valuePrepareFunction: (value: string) => {
            return value[0];
          },
        },
        icono: {
          title: "Icono",
          type: "html",
          valuePrepareFunction: (images: string) => {
            return `<img height="50px" class='table-thumbnail-img' src='${images}'/>`;
          },
        },
      },
      mode: "external",
    };
  }
}
