import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { LocalDataSource } from "ng2-smart-table";
import { GeneralService } from "../../services/general.service";
import { MainService } from "../../services/main.service";

@Component({
  selector: "ngx-compras",
  templateUrl: "./compras.component.html",
  styleUrls: ["./compras.component.scss"],
})
export class ComprasComponent implements OnInit {
  /** Arrya with  */
  public compras: any = [];

  /** Source for table */
  public source: LocalDataSource;

  /** Settings configuration for table */
  public settings: any = {};

  constructor(
    private mainService: MainService,
    private generalService: GeneralService
  ) {}

  ngOnInit(): void {
    this.getproductos();
    this.setSettings();
  }

  /**
   * Gets all productos
   */
  public getproductos() {
    this.mainService.get({ api: "api/compra-producto" }).then((res) => {
      this.compras = res;
      this.setAttributes();
      this.source = new LocalDataSource(this.compras);
    });
  }

  /**
   * Attributes in productos for table
   */
  public setAttributes() {
    this.compras.forEach((element) => {
      element["nombreUsuario"] = element.usuario
        ? `${element.usuario.nombre} ${element.usuario.apellido}`
        : "N/A";
      element["nombreProducto"] = element.producto
        ? element.producto.titulo
        : "N/A";
    });
  }

  /**
   * Settings configuration
   */
  public setSettings() {
    this.settings = {
      actions: {
        columnTitle: "Acciones",
        filter: true,
        add: false,
        edit: false,
        delete: false,
      },
      columns: {
        nombreUsuario: { title: "Usuario" },
        nombreProducto: { title: "Producto" },
      },
      mode: "external",
    };
  }
}
